* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}
body {
  color: #333;
  background-color: lightskyblue;
}

.list {
  max-width: 640px;
  width: calc(100% - 20px);
  border-radius: 10px;
  margin: 20px auto;
  background-color: lightcyan;
  padding: 10px;
}
.list h2 {
  font-size: 40px;
  margin-bottom: 10px;
  text-align: center;
}
.list form {
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.list form input {
  width: 400px;
  height: 40px;
  margin-bottom: 10px;
  border: 2px solid royalblue;
  border-radius: 10px;
}
.list form button {
  width: 40px;
  height: 30px;
}
.list ul {
  /* text-align: start; */
  background-color: white;
  border: 2px solid slateblue;
  border-radius: 10px;
}
/* nth-child(2n) => 2배수
nth-child(odd) => 홀수
nth-child(even) => 짝수 */

.list ul li:nth-child(odd):before {
  content: '🥚';

}
.list ul li:nth-child(even):before {
  content: '🐣';

}